body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root .ant-layout {
  background: none;
}

.info-container {
    background: #edf0f5;
    padding: 0;
    border: 1px solid #8e8e93;
}

.info-container > table {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
}

.heading {
    font-size: 1.1rem;
    text-transform: uppercase;
}

.heading th {
    text-align: left;
    padding: 20px 30px;
}

.body tr {
    border-bottom: 1px solid #dedede;
}

.body td {
    padding: 15px 30px;
    word-wrap: break-word;
    vertical-align: top;
    text-align: left;
    border-bottom: #000;
}

.upload-progress {
    position: absolute;
    width: 100%;
    height: 100%;
}

.upload-progress > div {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.caption {
    border: 1px solid #dedede;
    border-radius: 10px;
    padding: 10px;
    margin: 5px;
}

.caption-text {
    text-transform: capitalize;
    text-align: center;
    padding-top: 15px;
}

.color-data > div {
    margin-bottom: 5px;
}

.color-data > div:last-child {
    margin-bottom: 0;
}

.color-sample {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid #333;
    border-radius: 50%;
    vertical-align: middle;
}

.color-label {
    vertical-align: middle;
    margin-left: 5px;
}
.image-container {
    margin: 0;
    padding: 10px;
    border: 1px solid #8e8e93;
}

.image-container img {
    width: 100%;
}

.main-layout { 
    margin: 10px;
}

.title {
    color: #fff;
}
